import React from "react";


class Counter extends React.Component {
  constructor() {
    super()
    this.state = { value: '' }

    this.handleChange = this.handleChange.bind(this)
  }

  handleChange(event) {
    this.setState({value: event.target.value})
  }
  
  render() {
        return (
      <div>
        <h2>Grade converter </h2>
        <p>my grade in 4.5: 
        <input type="text" value={this.state.value} onChange={this.handleChange} />
        </p>
        
        <li>{this.state.gradet1} in /4.3</li>
        <li>{this.state.gradet2} in /4</li>
        <li>{this.state.gradet3} in /100%</li> 

        <button 
          onClick={() =>
            this.setState(prevState => ({
              gradet1: Math.round((((4.3 * prevState.value) /4.5) *100))/100.0,
              gradet2: Math.round((((4.0 * prevState.value) /4.5) *100))/100.0,
              gradet3: (prevState.value === '') ? 0 : (((prevState.value  - 1) * 40  / 3.5)  + 60).toFixed(1),
            }))
          }

        >

          Calc
        </button>
      </div>
    )
  }
}

export default Counter;


